/*-----------General---------*/
body {
  font-family: "Noto Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #fff;
  background-color: #0A2647;
  margin-top: 180px;
  overflow: scroll;
}

ul {
  list-style: none;
  outline: none;
}

/*------------Elements-----------*/
.navbar {
  height: 100px;
  border-bottom: 0.7rem solid rgb(251, 242, 224);
}

.navbar-title {
  font-size: 40px;
}

.loading-message {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.button-class {
  border: 3px solid;
  border-color: #fff;
  color: #fff;
  width: 225px;
  height: 250px;
  border-radius: 0.6rem;
  background-color: darkgoldenrod;
  text-transform: uppercase;
}

.button-class:hover {
  opacity: 0.7;
}

.button-pokemonImg {
  height: 80%;
}

.pokemon-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  height: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #051831;
  border-radius: 0.7rem;
  row-gap: 1rem;
}

/*-------Modal---------*/
.modal-content {
  display: flex;
  position: relative;
  flex-flow: column;
  background-color: darkgoldenrod;
  padding: 15px;
  height: 400px;
  width: 300px;
  margin: auto;
  border-radius: 0.6rem;
  border: 4px solid;
}

.modal-title {
  text-transform: uppercase;
  font-size: 24px;
}

.modal-body {
  padding: 0.5rem;
  text-align: center;
}

.modal-img {
  margin-top: 20px;
  width: 50%;
  height: 170px;
}

.modal-height,
.modal-types {
  padding-top: 20px;
  margin: 0;
}

/*-----Media-queries------*/
@media (max-width: 1200px) {
  .pokemon-list {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}